.burger{display:none}@media (max-width: 1600px){.header{background-color:rgba(0,0,0,0.4)}.navbar-main{padding:14px 0;max-width:1200px;margin:auto}.navbar-main .wrap{display:flex;justify-content:space-between;align-items:center}.navbar-main .navbar-nav{list-style:none;display:flex;width:45%;margin:0;padding:0;margin-left:auto}.navbar-main .nav-item{width:100%;text-align:right}.navbar-main .nav-item:not(.nav-item--logo){margin-top:5px}.navbar-main .nav-item .nav-link:after{content:"";display:block;width:0;border-bottom:1px solid #fff;margin:0 auto;-webkit-transition:all 0.3s linear 0s;-o-transition:all 0.3s linear 0s;transition:all 0.3s linear 0s}.navbar-main .nav-item .nav-link.active:after{width:100%}.navbar-main .nav-item .nav-link:hover:after{width:100%}.navbar-main .nav-item .nav-link{display:inline-block;color:#fff;font-family:'Poppins', sans-serif;font-style:normal;font-weight:600;font-size:14px;line-height:18px;letter-spacing:0.6px;text-decoration:none}.navbar-main .nav-item .nav-link:hover:after{width:100%}}@media only screen and (max-width: 1024px){body{overflow-x:hidden}.navbar-main .navbar-nav{position:absolute;right:0;height:calc(100vh + 20px);top:2vh;background-color:#00A031;flex-direction:column;align-items:center;width:0;transform:translateX(100%);transition:transform 0.1s ease-in;display:none}.navbar-main .navbar-nav.nav-active{display:flex;width:100%;transform:translateX(0%);justify-content:center;text-align:center;top:-20px}.navbar-main .navbar-nav.nav-active .nav-item{width:100%;text-align:center}.navbar-main .navbar-nav li{opacity:0}.navbar-main .burger{display:block}.navbar-main .burger div{width:25px;height:3px;margin:5px;background-color:#e2e2e2;transition:all 0.3s ease}}@keyframes navLinkFade{from{opacity:0;transform:translateX(50px)}to{opacity:1;transform:translateX(0px)}}.toggle .line1{transform:rotate(-45deg) translate(-5px, 6px)}.toggle .line2{opacity:0}.toggle .line3{transform:rotate(45deg) translate(-5px, -6px)}
